/*Custom CSS*/

.tweet_cont {
    float: left;
    overflow: hidden;
    width: 320px;
    /*height: 360px;*/
    margin: 15px 30px 15px 0;
    animation-name: expand, corner;
    animation-duration: .8s, .4s;
    animation-delay: 0s, 1s;
    /*border-radius: 30px 0 30px 0;*/
    background-color: #f9f9f9;
    /*box-shadow: 3px 3px 3px #bdbdbd;*/
    animation-fill-mode: forwards;
}

@-moz-keyframes expand {
    0% {
        height: 240px;
    }

    100% {
        height: 360px;
    }
}

@-moz-keyframes corner {
    0% {
        border-radius: 0 0 0 0;
        box-shadow: 0 0 0 #fff;
    }

    100% {
        border-radius: 30px 0 30px 0;
        box-shadow: 2px 2px 2px #bdbdbd;
    }
}

/*End of main container*/

.tweet_text_input {
    float: left;
}

.tweet_count_selector {
    float: right;
    margin: 0 118px 0 0;
}

/*Banner image container*/

.image_cont {
    width: 320px;
    height: 120px;
    padding-top: 25PX;
}

.image_cont > div.usr_banner {
    position: relative;
    z-index: 50;
    overflow: hidden;
    width: 320px;
    height: 100px;
}

.image_cont > div.usr_banner > img {
    width: auto;
    max-width: 100%;
    height: auto;
}

/*Profile image container*/

.image_cont > div.usr_img {
    position: relative;
    z-index: 100;
    top: -30px;
    left: 20px;
    overflow: hidden;
    width: 80px;
    height: 80px;
    border: 3px solid #f9f9f9;
    border-radius: 5px;
}

.image_cont > div.usr_img > img {
    width: auto;
    max-width: 100%;
    height: auto;
}

/*Text container for tweets*/

.text_cont {
    width: 300px;
    margin: auto;
    margin-top: 25px;
    padding: 20px;
    animation: fadein 2s;
    color: #303030;
}

@-moz-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.text_cont > h1 {
    font-size: 20px;
    font-weight: bold;
}

.text_cont > h2 {
    font-size: 15px;
    margin-top: -10px;
    color: #606060;
}

.text_cont > p {
    max-width: 280px;
    letter-spacing: .3px;
    text-overflow: ellipsis;
}

/*Top link bar*/

.link_title_top {
    float: right;
    margin-top: 3px;
    margin-right: 5px;
}

.link_title_top a {
    margin-left: 5px;
    text-decoration: none;
}

/*Plone content*/

.TitleTwitter {
    font-weight: 400;
    color: #4099ff;
    background-image: url(https://upload.wikimedia.org/wikipedia/en/9/9f/Twitter_bird_logo_2012.svg);
    background-repeat: no-repeat;
    background-position: 250px 0;
    background-size: 30px 25px;
}

.TitleTweeter {
    font-weight: 200;
    color: #303030;
}

/*Table info*/

.info_bar_cont table {
    float: right;
    width: 80px;
    margin-top: 10px;
    margin-right: 30px;
    border: none;
    background: none;
}

.info_bar_cont th {
    font-weight: 300;
    padding-left: 20px;
    color: #606060;
    border: none;
    background: none;
}

.info_bar_cont th span {
    font-size: 20px;
    font-weight: 400;
}

